@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap);
:root{
    --green:#27ae60;
    --black:#000000;
    --red:#f15252;
}

*{
    font-family: 'Nunito', sans-serif;
    margin: 0; padding: 0;
    box-sizing: border-box;
    outline: none;border: none;
    text-decoration: none;
    text-transform: capitalize;
    transition: all .2s linear;
}

*::selection{
    background: #f15252;
    background: var(--red);
    color: #fff;
    ;
}

html{
    font-size: 62.5%;
    overflow-x: hidden;
    scroll-padding-top: 6.5rem;
    scroll-behavior: smooth;
}

section{
    padding: 2rem 9%;
}

.home{
    display:contents;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height:100%;
    width: 100%;
}

.home .image{
    flex: 1 1 40rem;
    position: relative;
    top: -4%;
}

/* .home .content{
    flex: 1 1 20rem;
} */

.btnn{
    display: inline-block;
    
    margin-top: 1rem;
    background: #27ae60;
    background: var(--green);
    color: #fff;
    padding: .8rem 3rem;
    font-size: 1.7rem;
    text-align: center;
    font-weight: bolder;
    cursor: pointer;
}

.btn{
    display: inline-block;
    margin-top: 1rem;
    background: #27ae60;
    background: var(--green);
    color: #fff;
    padding: .8rem 3rem;
    font-size: 1.7rem;
    text-align: center;
    font-weight: bolder;
    cursor: pointer;
}

.home .content span{
    
   
    font-size: 3rem;
    color: #666;
}

.home .content h3{
    position: relative;
    left: 10%;
    top: -108%;
    font-size: 5rem;
    color:#000000;
    color:var(--black);
    
}

.home .image img{
    height: 100%;
    width: 100%;
    position:relative;
}


.btn:hover{
    background: #f15252;
    background: var(--red);
}

.btnn:hover{
    background: #f15252;
    background: var(--red);
}

.header{
    background: #eee;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2rem 9%;
}

.logom{
    height: 110px;
    display:flex;
    position: relative;
    justify-content: space-between;
    top: -170px;
    right: 102px;
    padding: 2rem 9%;
}

.logo{
    color:#000000;
    color:var(--black);
    font-weight: bolder;
    font-size: 3rem;
    position: relative;
    left: 40%;
}

