.header-2{
    background:#fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding:2rem 9%;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
    position: relative;
  }
  
  .header-2.active{
    position: fixed;
    top:0; left: 0; right:0;
    z-index: 10000;
  }

.header-2 .navbar a{
    padding: .5rem 1.5rem;
    font-size: 2rem;
    border-radius: .5rem;
    color: #000000;
    color: var(--black);
    position: relative;
    left: 80%;
}

.header-2 .navbar a:hover{
    background: #f15252;
    background: var(--red);
    color:#fff ;
}

.header-2 .icons a{
    font-size: 3rem;
    color:#f15252;
    color:var(--red);
    padding-left: 1rem;
}

.header-2 .icons a:hover{
    color: #27ae60;
    color: var(--green);
}

#menu-bar{
    font-size: 3rem;
    border-radius: .5rem;
    border: .1rem solid #000000;
    border: .1rem solid var(--black);
    padding: .8rem 1.5rem;
    color: #000000;
    color: var(--black);
    cursor: pointer;
    display: none;
}


.heading{
    text-align: center;
    color: #000000;
    color: var(--black);
    text-transform: uppercase;
    padding: 1rem;
    font-size: 3.5rem;
    padding-bottom: 2rem;
}

.heading span{
    color: #f15252;
    color: var(--red);
    text-transform: uppercase;
}

.category .box-container{
    display: flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    flex-wrap: wrap;
}

.category .box-container .box{
    border:.1rem solid rgba(0, 0, 0, .3) ;
    border-radius: .5rem;
    text-align: center;
    padding: 2rem;
    flex: 1 1 25rem;
}

.category .box-container .box img{
    width: 20rem;
    margin: .5rem 0;
}

.category .box-container .box h3{
    font-size: 3.5rem;
    color: #000000;
    color: var(--black);
}

.category .box-container .box p{
    font-size: 2rem;
    color:#666;
    padding: .5rem 0;
}

.category .box-container .box .btn{
    display: block;
}

.contact form{
    text-align: center;
    padding: 1rem;
    border:.1rem solid rgba(0, 0, 0, .3) ;
}

.contact form .inputBox{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.contact form .inputBox input, .contact form textarea{
    padding: 1rem;
    font-size: 1.7rem;
    background: #f7f7f7;
    text-transform: none;
    margin: 1rem 0;
    border: .1rem solid rgba(0, 0, 0, .3);
    width: 49%;
}

.contact form textarea{
    height: 20rem;
    resize: none;
    width: 100%;
}

.footer .box-container{
    display: flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    flex-wrap: wrap;
}

.footer .box-container .box{
    padding: 1rem 0;
    flex: 1 1 25rem;
}

.footer .box-container .box .share a{
    padding: 0;
    height: 4rem;
    width: 4rem;
    line-height: 4rem;
    text-align: center;
}

.footer .box-container .box h3{
    font-size: 2.5rem;
    color: #000000;
    color: var(--black);
}

.footer .box-container .box p{
    font-size: 1.3rem;
    color: #000000;
    color: var(--black);
    padding: .7rem 0;
}

.footer .box-container .box .open {
    padding: 1rem 0;
}

.footer .box-container .box .open h4{
    display: block;
    padding: .5rem 0;
    font-size: 1.7rem;
    color: #333;
}




/* adaptabilite */

@media(max-width:1200px){
    html{
        font-size: 55%;
        
    }
}

@media(max-width:991px){
    .header,
    .header-2{
        padding: 2rem;
    }

    .logom{
        display: none;
    }

    section{
        padding: 2rem;
    }
}

@media(max-width:768px){
    #menu-bar{
        display: inline;
        display: initial;
    }

    .logom{
        display: none;
    }

    .header-2 .navbar{
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        background: #f15252;
        background: var(--red);
        padding: 1rem 2rem;
        display: none;
    }

    .header-2 .navbar.active{
        display: inline;
        display: initial;
    }

    .header-2 .navbar a{
        font-size: 2.5rem;
        display: block;
        text-align: center;
        background:#fff;
        padding: 1rem;
        margin: 1.5rem 0;
    }

    .header-2 .navbar a:hover{
        background: #27ae60;
        background: var(--green);
    }

    .covert{
        display: none;
    }

    .home .content{
        text-align: center;
    }

    .home .content h3{
        font-size: 4rem;
    }


}

@media(max-width:450px){
    html{
        font-size: 50%;
    }

    .header{
        flex-flow: column;
    }

    .header .search-box-container{
        width: 100%;
        margin-top: 2rem;
    }

    .logo{
        font-size: 4rem;
    }
}
